import React from 'react' // 'Next는 따로 import해주지 않아도 되지만 구조상 사용함'
import Link from 'next/link';
import { Input,Form, Button, Card, Avatar, Icon } from 'antd';

const dummy = {
    isLoggedIn: true,
    imagePaths: [],
    mainPosts: [{
      User: {
        id: 1,
        nickname: '제로초',
      },
      content: '첫 번째 게시글',
      img: 'https://bookthumb-phinf.pstatic.net/cover/137/995/13799585.jpg?udate=20180726',
    }],
  };


const Home = () => {
    return (
       <div>
           {dummy.isLoggedIn && <Form style={{margin:'10px 0  20px'}}encType="multipart/form-data"> 
                <Input.TextArea MaxLength={140} placeholder="글을 입력" />
                <div style={{marginBottom : '30px'}}>
                    <Input type="file" multiple hidden/>
                    <Button>이미지 업로드</Button>
                    <Button type="primary" style={{float:'right'}} htmlType="submit">버튼</Button>
                </div>

                <div>
                    {dummy.imagePaths.map((v,i)=> {
                        return (
                            <div key={i} style={{display:'inline-block'}} >
                                <img src={'http://localhost:3065/'+ v} style={{width:'250px'}} alt={v} />
                                <Button>제거</Button>
                            </div>
                        )
                    })}
                </div>

                {dummy.mainPosts.map((c)=> {
                    return (
                        <Card
                        key={+c.createdAt}
                        cover={c.img && <img alt="example" src={c.img} />}
                        actions={[
                            <div type="retweet" key="retweet" />,
                            <div type="heart" key="heart" />,
                            <div type="message" key="message" />,
                            <div type="ellipsis" key="ellipsis" />,
                        ]}
                        extra={<Button>팔로우</Button>}
                      >
                            <Card.Meta
                                avatar={<Avatar>{c.User.nickname[0]}</Avatar>}
                                title={c.User.nickname}
                                description={c.content}
                                />
                        </Card>
                    );
                })}
            </Form>}
       </div>
    );
};

export default Home;